  clear: both; }

#cn-list .cn-category-breadcrumb {
  margin: 20px auto; }

#cn-list .cn-list-section-head h4 {
  font-size: 16px;
  padding: 0;
  margin: .8em 0; }

#cn-list .cn-alphaindex {
  font-size: 14px;
  font-weight: bold;
  margin: 12px 0;
  position: relative; }

#cn-list label {
  cursor: pointer; }

#wpadminbar #wp-admin-bar-connections-entry-edit > .ab-item:before {
  content: '\f464';
  top: 2px; }

/*
 * Image / Logo Styles
 */
#cn-list span.cn-image-style {
  border-radius: 4px;
  background-color: #FFFFFF;
  border: 1px solid #E3E3E3;
  box-sizing: border-box;
  display: inline-block;
  line-height: 0;
  margin: 0 0 5px;
  max-width: 100%;
  padding: 5px;
  width: auto; }

#cn-list span.cn-image-none {
  color: #AAAAAA;
  display: inline-block;
  font-weight: 700;
  line-height: normal;
  max-width: 100%;
  text-align: center;
  text-shadow: 1px 1px 0 #FFFFFF;
  position: relative; }
  #cn-list span.cn-image-none span {
    position: absolute;
    top: 1em;
    right: 0;
    left: 0;
    text-align: center; }

#cn-list img {
  box-shadow: none; }

img.cn-image {
  border: none !important;
  height: auto !important;
  left: 0 !important;
  margin: 0 !important;
  max-height: none !important;
  max-width: 100%;
  padding: 0 !important;
  position: relative !important;
  top: 0 !important;
  visibility: visible;
  width: auto;
  /* For proper IE8 support for max-width, width:auto needs to be defined. */ }

/*
 * Name and Org Styles
 */
