  display: inline-block; }

#cn-list span.org {
  display: block; }

#cn-list span.organization-name {
  display: block; }

#cn-list span.organization-unit {
  display: block; }

#cn-list span.title {
  display: block; }

#cn-list span.contact-name {
  display: block; }

#cn-list span.contact-label {
